import type { Profile, Tellme as Itellme } from "@prisma/client";
import { backgroundColorMap, colorMap, emojiMap } from "~/utils/constants";
import UserCircle from "./user-circle";

interface ITellme {
  profile: Profile;
  tellme: Partial<Itellme>;
}

export default function Tellme({ profile, tellme }: ITellme) {
  return (
    <div
      className={`flex ${
        backgroundColorMap[tellme.style?.backgroundColor || "RED"]
      }
    p-4 rounded-xl w-full gap-x-2 relative
    `}>
      <div>
        <UserCircle profile={profile} className='h-16 w-16' />
      </div>
      <div className='flex flex-col'>
        <p
          className={`${
            colorMap[tellme.style?.textColor || "WHITE"]
          } font-bold text-lg whitespace-pre-wrap break-all`}>
          {profile.firstName}
          {profile.lastName}
        </p>
        <p
          className={`${
            colorMap[tellme.style?.textColor || "WHITE"]
          } whitespace-pre-wrap break-all`}>
          {tellme.message}
        </p>
      </div>
      <div className='absolute bottom-4 right-4 bg-white rounded-full h-10 w-10 flex items-center justify-center text-2xl'>
        {emojiMap[tellme.style?.emoji || "THUMBSUP"]}
      </div>
    </div>
  );
}
